 .navbar{
    height: 5rem;
    width: 100vw;
    margin: 0;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between ;
    position: sticky;
    top: 0;
    z-index:3;
 }
 .listitem{
    margin: 1rem;
    cursor: pointer; 

}
.listitem:hover{
    color:yellow;
    padding-bottom: 0.5rem;
    border-bottom:3px solid yellow;
}

.menuimg
{
    object-fit:cover;
    height:1rem;
    width:1rem;
    margin:1rem;
    
}
.btn{
    background-color:aliceblue;
    border-radius:2rem;
    text-align:center;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content:center;
    padding: 0 1rem;
    
}